/* >>>>>>>>>>>>> MEDIA (medium screen) QUERIES <<<<<<<<<<<<<<< */

@media screen and (max-width: 1024px) {
  .header {
    height: 68vh;
  }
}

/* >>>>>>>>>>>>> MEDIA (small screen) QUERIES <<<<<<<<<<<<<<< */

@media screen and (max-width: 600px) {
  .header {
    height: 100vh;
  }

  .social__media,
  .scroll__down {
    display: none;
  }
}
